// Hood Variables
@admin-font: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
@hood-yellow: #d9b50b;
@hood-dark: #171818;
@sidebar-width: 295px;
@sidebar-link-color: #a7b1c2;
@border-radius-base: 4px;
@border-radius-large: 4px;
@border-radius-small: 4px;
@thumbnail-padding: 0px;
@thumbnail-caption-padding: 15px;
@grid-gutter-width: 15px;
@gray-base: #000;
@gray-darker: lighten(@gray-base, 13.5%); // #222
@gray-dark: lighten(@gray-base, 20%); // #333
@gray: lighten(@gray-base, 33.5%); // #555
@gray-light: lighten(@gray-base, 46.7%); // #777
@gray-lighter: lighten(@gray-base, 93.5%); // #eee

@brand-primary: @hood-yellow;
@brand-success: #5cb85c;
@brand-info: #5bc0de;
@brand-warning: #f0ad4e;
@brand-danger: #d9534f;

@btn-default-color: white;
@btn-default-bg: #5b5c5c;
@btn-default-border: darken(#5b5c5c, 3%);

@body-bg: #fff;
@text-color: @gray-dark;
@link-color: @hood-dark;
@link-hover-color: @hood-yellow;
@link-hover-decoration: none;

@panel-body-padding: @grid-gutter-width;
@modal-inner-padding: @grid-gutter-width;

//== Alerts
//
//## Define alert colors, border radius, and padding.

@alert-padding: 15px;
@alert-border-radius: @border-radius-base;
@alert-link-font-weight: bold;

@alert-success-bg: @brand-success;
@alert-success-text: white;
@alert-success-border: @brand-success;

@alert-info-bg: @brand-info;
@alert-info-text: white;
@alert-info-border: @brand-info;

@alert-warning-bg: @brand-warning;
@alert-warning-text: white;
@alert-warning-border: @brand-warning;

@alert-danger-bg: @brand-danger;
@alert-danger-text: white;
@alert-danger-border: @brand-danger;
